@import "../../../node_modules/nouislider/distribute/nouislider";

.ridestyler-showcase-suspension-selector-element {
    position: absolute;
    
    width: 100%;
    height: 40%;
    bottom: 25%;

    + .ridestyler-showcase-suspension-selector-element {
        left: 50%;
        right: 0;
    }

    &-label {
        text-align: center;
        color: white;
        margin-bottom: 10px;
    }

    .noUi-target {
        background: #777;
        border: none;
        box-shadow: none;
    }

    .noUi-value {
        margin-top: 1.5em;
        margin-left: 0.3em;
        font-size: 1em;

        &-sub {
            font-size: 0.75em;
        }
    }

    .noUi-horizontal {
        height: 10px;

        .noUi-handle {
            $size: 1.5em;
            left: -$size/2;
            top: -$size/2.5;
            width: $size;
            height: $size;
            border-radius: $size;
        }
    }

    .noUi-handle {
        background: #fff;
        border-radius: 10px;
        box-shadow: none;
        border: none;

        &:before, &:after {
            display: none;
        }
    }
}