@import "variables";
/*Mixins*/
@mixin transition($args...) {
	-webkit-transition: $args;
	-moz-transition: $args;
	-ms-transition: $args;
	-o-transition: $args;
	transition: $args;
}
@mixin box-sizing ($box-type) {
	-webkit-box-sizing: $box-type;
	-moz-box-sizing: $box-type;
	box-sizing: $box-type;
}
@mixin opacity($opacity) {
	opacity: $opacity;
	$opacity_10: $opacity*100;
	filter: alpha(opacity=$opacity_10);
}
@mixin middle($height: 16px) {
	position: absolute;
	height: $height;
	top: 50%;
	margin-top: - $height / 2;
}
@mixin center($width: 16px) {
	position: absolute;
	width: $width;
	left: 50%;
	margin-left: - $width / 2;
}
@mixin middle-center($width: 16px, $height: 16px) {
	position: absolute;
	width: $width;
	height: $height;
	left: 50%;
	top: 50%;
	margin-left: - $width / 2;
	margin-top: - $height / 2;
}
/*Placeholders*/
%full-screen {
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}
/*Modal Structure*/
.vsm-modal {
	position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1050;
    display: none;
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
    outline: 0;

	* {
		@include box-sizing(border-box);
	}

	&:active,
	&:focus {
		outline: 0;
	}

	&.fade .vsm-modal-dialog {
		@include transition(opacity .3s ease-out);
		-webkit-transform: translate(0,-25%);
		-ms-transform: translate(0,-25%);
		-o-transform: translate(0,-25%);
		transform: translate(0,-25%);
	}
	&.in .vsm-modal-dialog {
		-webkit-transform: translate(0,0);
		-ms-transform: translate(0,0);
		-o-transform: translate(0,0);
		transform: translate(0,0);
	}

	.vsm-modal-open & {
		overflow-x: hidden;
		overflow-y: auto;
	}
}

.vsm-hide {
	display: none;
}

.vsm-fade {
	@include opacity(0);
	@include transition(opacity .3s linear);

	&.in {
		@include opacity(1);
	}
}

.vsm-header {
	font-weight: $headers-font-weight;
	font-size: 16px;
	line-height: $header-line-height;
	margin: 18px auto 0;
}

.vsm-close {
	margin: 0;
	z-index: 2;
	position: absolute;
	right: $close-btn-padding;
	top: $close-btn-padding;
	padding: 0;
	cursor: pointer;
	background: transparent;
	border: 0;

	width: 24px;
	height: 24px;
	background-image: url(data:image/png;base64,);
	background-repeat: no-repeat;
	background-size: 24px 24px;

	&:active,
	&:focus {
		outline: 0;
	}
}

.vsm-back-arrow {
	background-image: url(data:image/png;base64,);
	width: 24px;
	height: 24px;
	background-repeat: no-repeat;
	background-size: 24px 24px;
	display: block;
}

.vsm-modal-dialog {
	position: relative;
    width: auto;
    margin: 10px;
}

.vsm-modal-content {
	position: relative;
    background-color: #fff;
    -webkit-background-clip: padding-box;
    background-clip: padding-box;
    border: 1px solid #999;
    border: 1px solid rgba(0,0,0,.2);
    border-radius: 6px;
    outline: 0;
    -webkit-box-shadow: 0 3px 9px rgba(0,0,0,.5);
    box-shadow: 0 3px 9px rgba(0,0,0,.5)
}

.vsm-modal-body {
	max-height: none;
	position: relative;
	overflow: hidden;
	height: 100%;
	min-height: 210px;
}

#vsm-backdrop {
	@extend %full-screen;
	position: fixed;
	background-color: #000;
	z-index: 9998;
}
/*Multi-Column*/
.vsm-columns {
	&:after,
	&:before {
		display: table;
		line-height: 0;
		content: "";
	}

	&:after {
		clear: both;
	}
	@at-root {
		[class*="vsm-column-"] {
			float: left;
			min-height: 1px;
			margin-left: 20px;
		}
	}

	[class*="vsm-column-"] {
		display: block;
		float: left;
		width: 100%;
		min-height: 30px;
		margin-left: 2.127659574468085%;

		&:first-child {
			margin-left: 0;
		}
	}

	&.vsm-line-step [class*="vsm-column-"] {
		margin-left: 0;
	}

	.vsm-column-4 {
		width: 23.404255319148934%;
	}

	&.vsm-line-step .vsm-column-4 {
		width: 25%;
	}

	.vsm-column-2 {
		width: 48.93617021276595%;
	}

	&.vsm-line-step .vsm-column-2 {
		width: 50%;
	}
}
/*Line step style*/
.vsm-line-step {
	position: relative;

	&.vsm-columns {
		[class*="vsm-column-"] {
			position: relative;

			&:before {
				content: '';
				display: block;
				left: 0;
				right: 0;
				background-color: $gray-light;
				@include middle(4px);
			}

			&.vsm-before:before {
				content: '';
				background-color: $red-normal;
			}
			&.vsm-before:after {
				left: -1000px;
				content: '';
				display: block;
				width: 1000px;
				z-index: 0;
				background-color: $red-normal;
				position: absolute;
				height: 4px;
				top: 50%;
				margin-top: -2px;
			}

			&.no-style:before {
				content: '';
				display: none;
			}

			&.active:before {
				background-color: $red-normal;
			}

			&:last-child:after {
				content: '';
				display: block;
				right: -1008px;
				width: 1000px;
				z-index: 0;
				background-color: $gray-light;
				@include middle(4px);
			}
		}

		&.vsm-single-step [class*="vsm-column-"]:last-child:after {
			right: -1000px;
		}
	}

	.dot-style {
		width: 16px;
		display: block;
		border-radius: 8px;
		background-color: $gray-light;
		@include middle(16px);

		&.active {
			background-color: $red-normal;
		}
	}

	.dot-before {
		left: -8px;
	}

	.dot-after {
		right: -8px;
	}

	a {
		text-decoration: none;
	}

	.dot-info {
		width: 200%;
		display: block;
		position: absolute;
		top: 18px;
		text-align: center;
		margin: 10px 0;
		font-size: 12px;
		line-height: $progress-line-height;
		max-height: 39px;
		overflow: hidden;
		color: $gray-med;
		font-weight: $lineinfo-font-weight;
		overflow-y: auto;

		&.selected-trim {
			font-size: 11px;
		}

		&.active {
			font-size: 14px;
			color: #333;
		}
	}

	.dot-info-before {
		left: -100%;
		text-align: center;
	}

	.dot-info-after {
		right: -48%;
	}

	.vehicle-result-name {
		font-size: 18px;
		font-weight: $vehicle-info-font-weight;
		margin: 0;
		line-height: $progress-line-height;
		margin-bottom: 5px;
		text-align: center;
	}

	.vehicle-result-size {
		font-size: 16px;
		color: $gray-dark;
		margin: 0;
		font-weight: 400;
	}
}
/*Other Elements*/
.vsm-modal-body .center-piece {
	max-width: 380px;
	margin: 0 auto;
	padding: $edge-padding;

	&.vehicle-result-wrap {
		position: relative;
		margin-top: 42px;
	}
}

.vsm-loaderIcon {
	@include middle-center(32px, 32px);
	background-image: url("data:image/gif;base64,");
}

.vsm-center {
	text-align: center;
}

.vsm-subheader {
	font-size: 17px;
	font-weight: 500;
	color: $gray-med;
	margin: 0 0 10px;
}

.vsm-group-wrap .vsm-group-list {
	margin: 0 0 15px;
}
.vsm-group-title {
	color: $gray-med;
	margin-top: 0;
	margin-bottom: 10px;
	font-weight: $headers-font-weight;
}

.vsm-third-container > a {
	width: 32%;
	margin-right: 2%;
	margin-bottom: 10px;

	&:nth-child(3n) {
		margin-right: 0;
	}
}

.vsm-half-container > a {
	width: 49%;
	margin-right: 2%;
	margin-bottom: 10px;

	&:nth-child(2n) {
		margin-right: 0;
	}
}

.vsm-one-container > a {
	width: 100%;
	margin-bottom: 10px;
	white-space: normal;
	&:first-child {
		margin-top: 10px;
	}
	&:last-child {
		margin-bottom: 0;
	}
}

.vsm-btn {
	white-space: nowrap;
	display: inline-block;
	vertical-align: middle;
	cursor: pointer;
	text-align: center;
	color: $gray-dark;
	background-color: $gray-light;
	font-size: 15px;
	overflow: hidden;
	text-overflow: ellipsis;
	padding: 8px 5px;
	text-decoration: none;
}

.vsm-full-width {
	width: 100%;
}

.vsm-find {
	margin-top: 10px;
}

.vsm-btn-gray {
	color: $gray-btn-text;
	background-color: $gray-dark;
}

.vsm-back-link {
	position: absolute;
	left: $close-btn-padding;
	top: $close-btn-padding;
	font-size: 22px;

	font-weight: 500;
	color: $gray-med;
	text-decoration: none;
	display: none;

	.vsm-has-back & {
		display: block;
	}
}

.vsm-final {
	display: none;

	.vsm-final-screen & {
		display: block;
	}
}

.vsm-final-screen .vsm-in-progress {
	display: none;
}

.vsm-table {
	display: table !important;
}

.vsm-table-cell {
	display: table-cell !important;
	vertical-align: middle;
	padding: 0 5px;
}

.vsm-selected-info {
	font-size: 14px;
	font-weight: $lineinfo-font-weight;
	max-height: 46px;
	overflow: hidden;
	display: block;
	text-decoration: underline;
	line-height: $progress-line-height;
}

.vsm-vehicle-display {
	max-width: 100%;
}

.vsm-options-select {
	width: 50%;
    height: 30px;
    border-radius: 0;
    display: block;
    margin: 10px auto;
}
@media screen and (min-width: 768px) {
	.vsm-modal-dialog {
		width: 440px;
		margin: 30px auto;
	}
	.modal-content {
		-webkit-box-shadow: 0 5px 15px rgba(0,0,0,.5);
		box-shadow: 0 5px 15px rgba(0,0,0,.5);
	}
}
